import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'cpo',
    loadChildren: () => import('./CPO/cpo.module').then(m => m.CpoModule),
    data: { title: 'CPO', titleI18n: 'cpo' },
  },
  {
    path: 'user',
    loadChildren: () => import('./User/user.module').then(m => m.UserModule),
    data: { title: 'User', titleI18n: 'user' },
  },
  {
    path: 'customer',
    loadChildren: () => import('./customer-registration/customer.module').then(m => m.CustomerModule),
    data: { title: 'Customer', titleI18n: 'customer' },
  },
  {
    path: 'vehicle',
    loadChildren: () => import('./Vehicle/vehicle.module').then(m => m.VehicleModule),
    data: { title: 'Vehicle', titleI18n: 'vehicle' },
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RegistrationModuleRoutingModule {}
